.simpleSlider {
  padding: 1em 0;
  min-width: 304px;
}
.simpleSlider_marks {
  padding-bottom: 2em;
}
.simpleSlider :global .rc-slider {
  position: relative;
  height: auto;
  width: 100%;
  border-radius: 6px;
  touch-action: none;
  box-sizing: border-box;
  padding: 0;
}
.simpleSlider :global .rc-slider-rail {
  background-color: #e1e1e1;
}
.simpleSlider :global .rc-slider-track {
  background-color: #1256aa;
}
.simpleSlider :global .rc-slider-handle {
  background-color: #1256aa;
  cursor: grab;
  border: none;
  width: 32px;
  height: 32px;
  transform: translate(-50%, calc(-50% + 2px)) !important;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 1;
  /* &:before {
    border-right-color: $white;
    border-left: 0;
  } */
  /* &:after {
    border-left-color: $white;
    border-right: 0;
  } */
}
.simpleSlider :global .rc-slider-handle:before, .simpleSlider :global .rc-slider-handle:after {
  display: none;
  /* content: '';
  display: block;
  border: 5px solid transparent;
  border-top-width: 3px;
  border-bottom-width: 3px;
  width: 0;
  height: 0;
  margin: 3px; */
}
.simpleSlider :global .rc-slider-handle-dragging {
  border: none !important;
  box-shadow: none !important;
}
.simpleSlider :global .rc-slider-handle:focus-visible {
  border: none;
  box-shadow: none;
}
.simpleSlider :global .rc-slider-mark-text {
  white-space: nowrap;
  font-size: 1.3em;
}
.simpleSlider :global .rc-slider-mark-text:first-child {
  transform: translateX(0%) !important;
}
.simpleSlider :global .rc-slider-mark-text:last-child {
  transform: translateX(-100%) !important;
}
.simpleSlider :global .rc-slider-dot {
  background-color: transparent;
  border-color: #707070;
  border-width: 1px;
  /* &:first-child,
